import React, {useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'


function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)

    const start = () => {
        stop()
        const id: number = window.setInterval(() => {
            setDate(new Date())
        }, 1000)
        setTimerId(id)

        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)

    }

    const stop = () => {
        clearTimeout(timerId)
        setTimerId(undefined)
        //   //    ,    (timerId <- undefined)

    }

    const onMouseEnter = () => { //   //     
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    /*const stringTime = date.getHours() +':'+ date.getMinutes() +':'+ date.getSeconds()  || <br/>*/
    const stringTime = date.toLocaleTimeString('ru-Ru') || <br/>


    /*const stringTime = date.getHours() +':'+ date.getMinutes() || <br/>*/
    /* ['ru-Ru'], {hour: '2-digit', minute:'2-digit'}*/

    // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  

    /*  const stringDate =  date.getDate() +'.'+ date.getMonth() +'.'+ date.getFullYear()|| <br/>*/
    const stringDate = date.toLocaleDateString('ru-Ru') || <br/>

    // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  


    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = date.toLocaleString('en-US', {weekday: 'long'}) || <br/> //  
    const stringMonth = date.toLocaleString('en-US', {month: 'long'}) || <br/> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}><strong>{stringTime}</strong></span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-date'}>{stringDate}</span>,{' '}
                            <span id={'hw9-month'}>{stringMonth}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={timerId !== undefined} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={timerId === undefined} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
